// hotplate variables
$hp-gutter: 1.25rem;
$hp-basefontsize: 16px;
$hp-fontsize: 1rem;

// breakpoints
$hp-bp--desktopb: 1140px;
$hp-bp--desktop: 960px;
$hp-bp--tablet: 768px;
$hp-bp--phablet: 620px;
$hp-bp--phoneh: 480px;
$hp-bp--phone: 320px;

// easier to remember aliases
$xl: $hp-bp--desktopb;
$lg: $hp-bp--desktop;
$md: $hp-bp--tablet;
$sm: $hp-bp--phablet;
$xs: $hp-bp--phoneh;
$xxs: $hp-bp--phone;


// mobile-first media query
@mixin media($mediawidth: $hp-bp--tablet) {
	@media (min-width: $mediawidth) {
	@content;
	}
}

// use this to define non-first mobile first columns
@mixin column($numberofcolumns: 3, $howmanycolumns: 1) {
	width: 100% / $numberofcolumns * $howmanycolumns;
}

